import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
import Backend from 'i18next-http-backend';

i18n
  // Load translation using http -> see /public/locales (default)
  // Will make requests to `/locales/en/translation.json` etc.
  .use(Backend)
  // Detect user language
  // Learn more: https://github.com/i18next/i18next-browser-languageDetector
  .use(LanguageDetector)
  // Pass the i18n instance to react-i18next.
  .use(initReactI18next)
  // Initialize i18next
  // For all options read: https://www.i18next.com/overview/configuration-options
  .init({
    fallbackLng: 'en',
    debug: process.env.NODE_ENV === 'development',
    load: 'languageOnly', // load only the language code, not the country dialect
    interpolation: {
      escapeValue: false, // Not needed for React as it escapes by default
    },
    react: {
      useSuspense: true, // Use Suspense to handle the loading state of translation files
    },
    backend: {
      // Path where resources get loaded from. Can be a function.
      loadPath: '/locales/{{lng}}/translation.json',
    },
  });

export default i18n;
